
@mixin dark-overlay {
  content: '';
  position: absolute;
  left: 0;
  top:0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
}

@mixin color-overlay($color, $opacity) {
  content: '';
  position: absolute;
  left: 0;
  top:0;
  width: 100%;
  height: 100%;
  background-color: $color;
  opacity: $opacity;
}
